import { Injectable } from '@angular/core';
import { Store, Action } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { AppStore } from './app-store';

const UPDATE_BENEFICIAIRE = 'UPDATE_BENEFICIAIRE';

export interface BeneficiaireProfile {
  account: string;
  lastname: string;
  firstname: string;
  agenceBes: string;
  agree: string;
  newsletterInscription: string;
}

const initialState = {
  account: '',
  lastname: '',
  firstname: '',
  agenceBes: '',
  agree: '',
  newsletterInscription: ''
};

export const beneficiaire = (state: BeneficiaireProfile = initialState, action: Action) => {
  switch (action.type) {
    case UPDATE_BENEFICIAIRE:
      return action.payload;
    default:
      return state;
  }
};

@Injectable()
export class BeneficiaireService {

  beneficiaire$: Observable<BeneficiaireProfile> = this.store.select('beneficiaire');

    
  constructor(private store: Store<AppStore>) { }

  updateBeneficiaire(beneficiaire) {
    this.store.dispatch({ type: UPDATE_BENEFICIAIRE, payload: beneficiaire });
  }
}



