import { Component } from '@angular/core';

@Component({
  templateUrl: 'register.component.html'
})
export class RegisterComponent {
  public model: any = {
    beneficiare: {}
  };
  public step: number = 1;
  public hidden: boolean = false;
  public success: boolean = false;
  public submitted: number = 0;
  public error: string = '';
  constructor() { }

  submittedStep(nbr: number): void {
    this.submitted = nbr;
  }

  nextStep(valid: boolean): void {
    if (valid) {
      if (this.step < 6) {
        if (this.step == 1) {
          if (this.model.email == "relamrani@soprabanking.com") {
            this.error = "adresse email existe déja";
            return;
          } 
        }
          this.step++;
          this.error = "";
      
        console.log(this.error);
      }
    }
  }
  hideBeneficiare(): void {
    this.hidden = !this.hidden;
  }
  register(): void {
    alert('here');
    this.success = true;
  }
  prevStep(): void {
    if (this.step > 0) {
      this.step--;
    }

  }
}



