import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { ReCaptchaModule } from 'angular2-recaptcha';
import { ResetComponent } from './components/reset.component';
import { LoginComponent } from './components/login.component';
import { NewsletterComponent } from './components/newsletter.component';
import { RegisterComponent } from './register.component';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PagesRoutingModule } from './home-routing.module';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { EqualValidator } from './validate-equal.directive';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { user, UserService } from './state/user';
import { health, HealthService } from './state/health';
import { address, AddressService } from './state/address';
import { beneficiaire, BeneficiaireService } from './state/beneficiaire';
import { RemoteService } from './state/remote';

import { ResultComponent } from './result/result.component';
import { FormsComponent } from './forms/forms.component';
import { UserComponent } from './forms/user/user.component';
import { HealthComponent } from './forms/health/health.component';
import { SummaryComponent } from './forms/summary/summary.component';
import { AddressComponent } from './forms/address/address.component';
import { BeneficiaireComponent } from './forms/beneficiaire/beneficiaire.component';
import { CapitalizePipe } from './forms/summary/capitalize.pipe';

import { MyAccountNumberFormatterDirective } from './../pipe-transform/pipe-account-number.directive'
import { MyAccountNumberPipe } from './../pipe-transform/pipe-account-number.pipe';

import { AgmCoreModule } from 'angular2-google-maps/core';
import { LimitInputDirective } from './limit-to.directive';
@NgModule({
  imports: [
    PagesRoutingModule,
    FormsModule,
     AgmCoreModule.forRoot({
      apiKey: 'AIzaSyDs3MbHrfAKNiurDnNZ8hIPB7zTS1fOxN8'
    }),
    BsDropdownModule.forRoot(),
    NgbModule.forRoot(),
    CommonModule,
    ReactiveFormsModule,
    ReCaptchaModule,
    StoreModule.provideStore({ user, health, address, beneficiaire }),
  ],
  declarations: [
    ResetComponent,
    NewsletterComponent,
    LimitInputDirective,
    LoginComponent,
    EqualValidator,
    RegisterComponent,
    ResultComponent,
    FormsComponent,
    BeneficiaireComponent,
    AddressComponent,
    UserComponent,
    HealthComponent,
    SummaryComponent,
    CapitalizePipe,
    MyAccountNumberFormatterDirective,
    MyAccountNumberPipe
  ],
  providers: [
    UserService,
    HealthService,
    RemoteService,
    AddressService,
    MyAccountNumberPipe,
    BeneficiaireService]
})
export class HomeModule { }



