import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { BeneficiaireService, BeneficiaireProfile } from "../../state/beneficiaire";
import { AppStore } from '../../state/app-store';
import { Store } from '@ngrx/store';
import { UserService, UserProfile } from '../../state/user';
import { AddressService, AddressProfile } from "../../state/address";
@Component({
  selector: 'app-beneficiaire',
  templateUrl: './beneficiaire.component.html',
  styleUrls: ['./beneficiaire.component.css']
})
export class BeneficiaireComponent implements OnInit {
  form: FormGroup;
  public hidden: boolean = false;
  public hiddenBen: boolean = false;
  public success: boolean = false;
  public user: any;
  lat: number = 48.872418;
  lng: number = 2.318366;

  markers: marker[] = [
    {
      lat: 48.872418,
      lng: 2.318366,
      label: '',
      icon: "assets/img/marker.png",
      draggable: true
    },
    {
      lat: 47.336907,
      lng: 5.044692,
      label: '',
      icon: "assets/img/marker.png",
      draggable: false
    },
    {
      lat: 48.580245,
      lng: 7.770395,
      label: '',
      icon: "assets/img/marker.png",
      draggable: true
    }
  ]
  constructor(
    private store: Store<AppStore>,
    private beneficiaireService: BeneficiaireService,
    private addressService: AddressService,
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router
  ) { }

  ngOnInit() {

    this.beneficiaireService.beneficiaire$
      .subscribe(this.initForm.bind(this));

    // this.addressService.address$
    //   .subscribe(address => {
    //     if (address.address.length > 0) {
    //       this.form.patchValue({ agenceBes: 'laon' })

    //     }

    //   });
  }

  initForm(beneficiaire: BeneficiaireProfile) {
    this.form = this.fb.group({
      account: [beneficiaire.account, [Validators.required, Validators.pattern(/([0-9]{21})$/)]],
      lastname: [beneficiaire.lastname, Validators.required, { disabled: false }],
      firstname: [beneficiaire.firstname, Validators.required, { disabled: false }],
      agenceBes: [beneficiaire.agenceBes, Validators.required],
      newsletterInscription: [beneficiaire.newsletterInscription],
      agree: [beneficiaire.agree],

    });
  }
  hideMap(): void {
    this.hidden = !this.hidden;
  }
  hideBeneficiare(): void {
    this.hiddenBen = !this.hiddenBen;
    if (this.hiddenBen === true) {
      this.form.get('lastname').disable();
      this.form.get('firstname').disable();
      this.user = this.userService.user$.subscribe(user => {
        this.form.get('lastname').patchValue(user.lastName);
        this.form.get('firstname').patchValue(user.firstName);
      });

    } else {
      this.form.get('lastname').patchValue('');
      this.form.get('firstname').patchValue('');
      this.form.get('firstname').enable();
      this.form.get('lastname').enable();
    }
  }

  back() {
    this.router.navigateByUrl('address');
  }
  backToHome() {
    this.router.navigateByUrl('dashboard/dashboard');
  }

  next() {
    this.beneficiaireService.updateBeneficiaire(this.form.value);
    this.success = true;
  }

  clickedMarker(label: string, index: number) {
    alert('ok');
  }
}

interface marker {
  lat: number;
  lng: number;
  label?: string;
  icon?: string;
  draggable: boolean;
}


