import { Directive, HostListener, ElementRef, OnInit } from "@angular/core";
import { MyAccountNumberPipe } from "./pipe-account-number.pipe";


@Directive({ selector: "[MyAccountNumberFormatter]" })
export class MyAccountNumberFormatterDirective implements OnInit {

  private el: HTMLInputElement;

  constructor(
    private elementRef: ElementRef,
    private cardNumberPipe: MyAccountNumberPipe
  ) {
    this.el = this.elementRef.nativeElement;
  }

  ngOnInit() {
    this.el.value = this.cardNumberPipe.transform(this.el.value);
  }

  @HostListener("focus", ["$event.target.value"])
  onFocus(value) {
    this.el.value = this.cardNumberPipe.parse(value); // opossite of transform
  }

  @HostListener("blur", ["$event.target.value"])
  onBlur(value) {
    this.el.value = this.cardNumberPipe.transform(value);
  }

}


