import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService, UserProfile } from '../../state/user';


@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  form: FormGroup;
  public step: number = 1;
  public hidden: boolean = false;
  public success: boolean = false;
  public submitted: number = 0;
  public error: string = '';
  constructor(
    private userService: UserService,
    private fb: FormBuilder,
    private router: Router
  ) { }

  ngOnInit() {
    this.userService.user$
      .subscribe(user => {
        this.initForm(user);
        this.handleFormChanges();
      });
  }

  initForm(user: UserProfile) {
    this.form = this.fb.group({
      lastName: [user.lastName, Validators.required],
      firstName: [user.firstName, Validators.required],
      // lastName: [user.lastName, Validators.required],
      email: [user.email, Validators.compose([Validators.required, Validators.pattern(/^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i)])],
      confirmEmail: [user.confirmEmail, Validators.compose([Validators.required, Validators.pattern(/^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i)])]
   }, {validator: this.matchingPasswords('email', 'confirmEmail')});
  }
  matchingPasswords(passwordKey: string, confirmPasswordKey: string) {
  return (group: FormGroup): {[key: string]: any} => {
    let password = group.controls[passwordKey];
    let confirmPassword = group.controls[confirmPasswordKey];

    if (password.value !== confirmPassword.value) {
      return {
        mismatchedPasswords: true
      };
    }
  }
}
  handleFormChanges() {
    // this.form.get('isAwesome').valueChanges.subscribe(value => {
    //   if (!value) {
    //     this.form.get('awesomenessReason').setValue('');
    //   }
    // });
  }

  next() {
    this.userService.updateUser(this.form.value);
    this.router.navigateByUrl('health');
  }
}



