import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AddressService, AddressProfile } from "../../state/address";
import { AppStore } from '../../state/app-store';
import { Store } from '@ngrx/store';
@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.css']
})
export class AddressComponent implements OnInit {
  form: FormGroup;

  constructor(
    public store: Store<AppStore>,
    private addressService: AddressService,
    private fb: FormBuilder,
    private router: Router
  ) { }

  ngOnInit() {
    this.addressService.address$
      .subscribe(this.initForm.bind(this));
  }

  initForm(address: AddressProfile) {
     
    this.form = this.fb.group({
      address: [address.address, Validators.required],
      countryOfBirth: [address.countryOfBirth, Validators.required],
      codePostal: [address.codePostal, Validators.required],
      phone: [address.phone, [Validators.required,  Validators.pattern( /([0-9]{10})$/)]],
      email: [address.email],
      cityOfBirth: [address.cityOfBirth]
    });
  }

  back() {
    this.router.navigateByUrl('health');
  }

  next() {
    this.addressService.updateAddress(this.form.value);
    this.router.navigateByUrl('beneficiaire');
  }
}



