
import { FormControl } from '@angular/forms';


export function validateBirthday(c: FormControl): { [key: string]: any } {

    let know = new Date().getTime();
    let date1 = c.value.year + '-' + c.value.month + '-' + c.value.day;
    let date = new Date(date1).getTime();
    date = date + 24 * 60 * 60 * 1000 * 365 * 18

    if (know < date) {
         return {
        validateBirthday: true
      };
    }
}

export function validateDate(c: FormControl): { [key: string]: any } {

    let know = new Date().getTime();
    let date1 = c.value.year + '-' + c.value.month + '-' + c.value.day;
    let date = new Date(date1).getTime();

    if (know > date) {
         return {
        validateDate: true
      };
    }
}



