import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HealthService, HealthProfile } from "../../state/health";
import { AppStore } from '../../state/app-store';
import { Store } from '@ngrx/store';
import { validateDate, validateBirthday } from './date-validator';
import {NgbDatepickerConfig, NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
@Component({
  selector: 'app-health',
  templateUrl: './health.component.html',
  styleUrls: ['./health.component.css'],
  providers: [NgbDatepickerConfig]
})
export class HealthComponent implements OnInit {
  form: FormGroup;

  constructor(
    public store: Store<AppStore>,
    private healthService: HealthService,
    private fb: FormBuilder,
    private router: Router,
    config: NgbDatepickerConfig
  ) { 
     config.minDate = {year: 1920, month: 1, day: 1};

  }

  ngOnInit() {
    this.healthService.health$
      .subscribe(this.initForm.bind(this));
  }

  initForm(health: HealthProfile) {
    this.form = this.fb.group({
      gender: [health.gender, Validators.required],
      birthday: [health.birthday, [Validators.required, validateBirthday]],
      birthplace: [health.birthplace, Validators.required],
      identityDocument: [health.identityDocument, Validators.required],
      identityNumber: [health.identityNumber, Validators.required],
      placeOfIssue: [health.placeOfIssue, Validators.required],
      dateOfIssue: [health.dateOfIssue, Validators.required],
      expirDate: [health.expirDate, [Validators.required, validateDate]],


    });
  }

  back() {
    this.router.navigateByUrl('register');
  }

  next() {
    this.healthService.updateHealth(this.form.value);
    this.router.navigateByUrl('address');
  }
}



