import { Injectable } from '@angular/core';
import { Store, Action } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { AppStore } from './app-store';

const UPDATE_HEALTH = 'UPDATE_HEALTH';

export interface HealthProfile {
  gender: string;
  birthday: string;
  birthplace: string;
  identityDocument: string;
  identityNumber: string;
  placeOfIssue: string;
  dateOfIssue: string;
  expirDate: string;
}

const initialState = {
  gender: '',
  birthday: '',
  birthplace: '',
  identityDocument: '',
  identityNumber: '',
  placeOfIssue: '',
  dateOfIssue: '',
  expirDate: ''
};

export const health = (state: HealthProfile = initialState, action: Action) => {
  switch (action.type) {
    case UPDATE_HEALTH:
      return action.payload;
    default:
      return state;
  }
};

@Injectable()
export class HealthService {
  health$: Observable<HealthProfile> = this.store.select('health');
  constructor(private store: Store<AppStore>) { }

  updateHealth(health) {
    this.store.dispatch({ type: UPDATE_HEALTH, payload: health });
  }
}



