import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ResetComponent } from './components/reset.component';

import { NewsletterComponent } from './components/newsletter.component';
import { LoginComponent } from './components/login.component';
import { RegisterComponent } from './register.component';

import { UserComponent } from './forms/user/user.component';
import { HealthComponent } from './forms/health/health.component';
import { SummaryComponent } from "./forms/summary/summary.component";
import { AddressComponent } from "./forms/address/address.component";
import { BeneficiaireComponent } from "./forms/beneficiaire/beneficiaire.component";

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Example Pages'
    },
    children: [
      {
        path: 'reset',
        component: ResetComponent,
        data: {
          title: 'Reset Password'
        }
      },
      {
        path: 'newsletter',
        component: NewsletterComponent,
        data: {
          title: 'newsletter'
        }
      },
      {
        path: 'login',
        component: LoginComponent,
        data: {
          title: 'Login Page'
        }
       },
      // {
      //   path: 'register',
      //   component: RegisterComponent,
      //   data: {
      //     title: 'Register Page'
      //   }
      // },
      {
        path: 'register',
        component: UserComponent,
        data: {
          title: 'user Page'
        }
      },
      {
        path: 'health',
        component: HealthComponent,
        data: {
          title: 'user Page'
        }
      },
      {
        path: 'address',
        component: AddressComponent,
        data: {
          title: 'address user Page'
        }
      },
      {
        path: 'beneficiaire',
        component: BeneficiaireComponent,
        data: {
          title: 'address user Page'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule {}



